html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "RoadRadio", sans-serif;
  font-weight: 500;
  color: var(--color-dark);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 375px;
  min-width: 1170px;
}

@media (max-width: 576px) {
  .container {
    padding: 0 15px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.circle-link {
  border-radius: 100%;
  padding: 18px;
  width: 50px;
  height: 50px;
}

.circle-link svg {
  width: 14px;
  height: 14px;
  fill: currentColor;
}

.yellow-btn {
  border: none;
  border-radius: 50px;
  box-shadow: 0 4px 0 0 #dfc220;
  font-weight: 700;
  font-size: 16px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: var(--color-bunker);
  background: linear-gradient(261.6deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 56.28%), linear-gradient(180deg, #fff96e 0%, #ffe036 100%);
}

.notification {
  border-radius: 100%;
  padding: 88px 40px 34px;
  width: 170px;
  height: 170px;
  text-align: center;
  background-position: center 30%;
  background-repeat: no-repeat;
  background-color: #f02a59;
}

.notification a {
  font-weight: 700;
  font-size: 12px;
  line-height: 130%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: var(--color-white);
}

.header {
  width: 100%;
  height: 425px;
  background-image: url("../../img/header-bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.header__container {
  min-width: 1170px;
}

.header__top {
  display: flex;
  align-items: start;
  margin-bottom: 21px;
  padding-top: 40px;
}

.header__contacts {
  margin-left: 90px;
  padding-left: 42px;
  background-image: url("../../img/phone.png");
  background-position: 0 0;
  background-size: 28px;
  background-repeat: no-repeat;
}

.header__logo {
  align-self: flex-start;
}

.header-contacts__tel {
  min-width: 228px;
  font-size: 28px;
}

.header-contacts__descr {
  margin-bottom: 10px;
  font-size: 13px;
}

.header__feedback {
  margin-left: 20px;
}

.header__social {
  margin-left: 40px;
}

.contacts {
  display: flex;
  flex-direction: column;
  color: var(--color-white);
}

.contacts__tel {
  font-weight: 700;
}

.contacts__descr {
  font-family: "Mont", sans-serif;
  font-weight: 600;
}

.contacts-messengers {
  display: flex;
  align-items: center;
  height: 30px;
}

.contacts-messengers__item {
  height: 100%;
}

.contacts-messengers__link {
  display: block;
  width: 30px;
  height: 100%;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.contacts-messengers__link--whats {
  background-image: url("../../img/WhatsApp.png");
}

.contacts-messengers__link--viber {
  background-image: url("../../img/Viber.png");
}

.contacts-messengers__link--telegram {
  background-image: url("../../img/Telegram.png");
}

.feedback {
  width: 380px;
}

.feedback .feedback-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.feedback .feedback-list__item {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.feedback .feedback-list__link {
  margin-bottom: 12px;
  border-radius: 50px;
  padding-top: 17px;
  height: 50px;
  font-weight: 500;
  font-size: 12px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.feedback .feedback-list__link--call {
  width: 165px;
  color: var(--color-white);
  background-color: var(--color-white-brown);
}

.feedback .feedback-list__link--question {
  padding-left: 28px;
  width: 205px;
  color: #4b413b;
  background-image: url("../../img/Question.png");
  background-position: 28px 16px;
  background-size: 16px;
  background-repeat: no-repeat;
  background-color: var(--color-white);
}

.feedback .feedback-list__descr {
  font-family: "Mont", sans-serif;
  font-weight: 600;
  font-size: 13px;
  color: var(--color-white);
}

.social__list {
  display: flex;
  align-items: center;
  justify-content: start;
  margin-bottom: 12px;
}

.social__item {
  width: 50px;
  height: 50px;
}

.social__link {
  display: block;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.social__link--vk {
  background-image: url("../../img/VK.png");
}

.social__link--inst {
  background-image: url("../../img/Instagram.png");
}

.social__link--youtube {
  background-image: url("../../img/YouTube.png");
}

.social__descr {
  padding-left: 20px;
  max-width: 130px;
  font-family: "Mont", sans-serif;
  font-weight: 600;
  font-size: 13px;
  line-height: 160%;
  text-align: center;
  color: var(--color-white);
}

.header-nav__list {
  display: flex;
  align-items: center;
  justify-content: start;
}

.header-nav__item {
  position: relative;
  height: 22px;
  font-weight: 700;
  font-size: 16px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: var(--color-white);
}

.header-nav__item:not(:last-child) {
  margin-right: 50px;
}

.header-nav__item--dropdown {
  display: flex;
  flex-direction: column;
  user-select: none;
}

.header-nav__item--dropdown .header-nav__link {
  display: flex;
  align-items: center;
}

.header-nav__item--dropdown img {
  margin-left: 15px;
  width: 14px;
  height: 14px;
}

.header-nav__item--dropdown:hover .dropdown-link {
  display: block;
}

.dropdown-link {
  position: absolute;
  left: 0;
  top: calc(100% + 2px);
  z-index: 20;
  display: none;
  border-radius: 2px;
  padding: 10px 0 10px 20px;
  width: 100%;
  background-color: var(--color-white-brown);
}

.dropdown-link__item {
  font-size: 14px;
}

.dropdown-link__item:not(:last-child) {
  margin-bottom: 15px;
}

.offer {
  max-height: 1170px;
  background-image: url("../../img/offer-section-bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.offer__container {
  position: relative;
  height: 700px;
  min-width: 1170px;
  background-image: url("../../img/bg-offer.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transform: translateY(-200px);
}

.offer__content {
  margin-right: 0;
  margin-left: auto;
  padding: 65px 0;
  width: 46%;
}

.offer__guarantee {
  position: absolute;
  left: 35%;
  top: 20%;
  background-image: url("../../img/guarantee.png");
}

.offer__title {
  margin-bottom: 25px;
  max-width: 470px;
  font-weight: 700;
  font-size: 48px;
  color: var(--color-white);
}

.offer__descr {
  margin-bottom: 42px;
  max-width: 420px;
  font-family: "Mont", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 160%;
  color: var(--color-white);
}

.offer__pick-proj {
  display: flex;
  flex-direction: column;
  width: 315px;
  text-align: center;
}

.offer__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 14px;
  width: 100%;
  height: 80px;
}

.offer__btn img {
  margin-right: 20px;
  width: 21px;
  height: 22px;
}

.offer-categories {
  min-width: 1170px;
  transform: translateY(-200px);
}

.offer-categories__list {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
}

.offer-categories__item {
  padding-top: 40px;
  padding-right: 50px;
  padding-bottom: 45px;
  padding-left: 50px;
  width: 33.3%;
  height: 335px;
}

.offer-categories__item:not(:last-child) {
  border-right: 1px solid #e3eaed;
}

.offer-categories__title {
  margin-bottom: 15px;
  max-width: 150px;
  font-size: 28px;
  color: var(--color-bunker);
}

.offer-categories__price {
  font-family: "Mont", sans-serif;
  font-weight: 600;
  font-size: 18px;
  color: var(--color-bunker);
}

.offer-categories__link {
  margin-top: 30px;
  border: 1px solid currentColor;
  color: var(--color-mystic);
}

.offer-statistic {
  min-width: 1170px;
  transform: translateY(-200px);
}

.offer-statistic__list {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
}

.offer-statistic__item {
  display: flex;
